
import { defineComponent, ref } from 'vue';
import { QuickFetch, QuickData } from '@/quick';
import { useRoute } from 'vue-router';

export default defineComponent({
  setup(props, context) {
    const data: any = ref([]);
    const qf = new QuickFetch();
    const qd = new QuickData();
    const route = useRoute();
    qf.Url(`general/${route.params.gameCode}/selected-event/${route.params.gameId}`).Get().then((res: any) => {
      for (let i = 0; i < res.length; i++) {
        data.value.push(res[i].event_id);
      }
    });
    const eventList: any = ref([]);
    qf.Url('event/list').GetAll(eventList);
    function submitAll() {
      qf.Url(`general/${route.params.gameCode}/selected-event/set/${route.params.gameId}`).Post({ data: JSON.stringify(data.value) }).then((res: any) => {
        if (res.message === 'done') {
          qd.Alert('已儲存');
          context.emit('close_modal');
        }
      });
    }
    return {
      data,
      eventList,
      submitAll,
    };
  },
});
