
import { defineComponent, ref } from 'vue';
import { QuickFetch, QuickData } from '@/quick';
import { useRoute } from 'vue-router';

export default defineComponent({
  setup(props, context) {
    const data: any = ref([]);
    const qf = new QuickFetch();
    const qd = new QuickData();
    const route = useRoute();
    qf.Url(`general/${route.params.gameCode}/date/${route.params.gameId}`).GetAll(data);
    function submitAll() {
      for (let i = 0; i < data.value.length; i++) {
        if (data.value[i].date === '' || data.value[i].day === '') {
          qd.Alert('不得有空欄位');
          return;
        }
      }
      qf.Url(`general/${route.params.gameCode}/date/set/${route.params.gameId}`).Post({ data: JSON.stringify(data.value) }).then((res: any) => {
        if (res.message === 'done') {
          qd.Alert('已儲存');
          context.emit('close_modal');
        }
      });
    }
    return {
      data,
      submitAll,
    };
  },
});
