
import { defineComponent, ref } from 'vue';
import { QuickFetch, QuickData } from '@/quick';
import { useRoute } from 'vue-router';

export default defineComponent({
  setup(props, context) {
    const qf = new QuickFetch();
    const qd = new QuickData();
    const route = useRoute();
    const data: any = ref([]);
    const eventList: any = ref([]);
    qf.Url(`general/${route.params.gameCode}/selected-event/${route.params.gameId}`).GetAll(eventList).then(() => {
      qf.Url(`general/${route.params.gameCode}/event-params/${route.params.gameId}`).Get().then((res: any) => {
        for (let i = 0; i < eventList.value.length; i++) {
          const temp: any = [];
          for (let j = 0; j < res.length; j++) {
            if (eventList.value[i].event_id === res[j].event_id) {
              temp.push(res[j].game_division_id);
            }
            console.log(temp);
          }
          data.value.push({ event_id: eventList.value[i].event_id, selected_division: temp });
        }
      });
    });
    const divisionList: any = ref([]);
    qf.Url(`general/${route.params.gameCode}/game-division/${route.params.gameId}`).GetAll(divisionList);
    function submitAll() {
      const temp: any = [];
      let count = 1;
      for (let i = 0; i < data.value.length; i++) {
        for (let j = 0; j < divisionList.value.length; j++) {
          if (data.value[i].selected_division.includes(divisionList.value[j].game_division_id)) {
            temp.push({ id: count, game_division_id: divisionList.value[j].game_division_id, event_id: data.value[i].event_id });
            count++;
          }
        }
      }
      qf.Url(`general/${route.params.gameCode}/event-params/set/${route.params.gameId}`).Post({ data: JSON.stringify(temp) }).then((res: any) => {
        if (res.message === 'done') {
          qd.Alert('已儲存');
          context.emit('close_modal');
        }
      });
    }
    return {
      data,
      eventList,
      divisionList,
      submitAll,
    };
  },
});
